@import url('https://fonts.googleapis.com/css2?family=Grandstander:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@1,300&family=Grandstander:wght@200&display=swap');
.heading{
  text-align: center;
  font-family: 'Grandstander', cursive;
  padding: 10px;
  color:  #00b300;
  margin-top: 10px;
}
.owner{
  width:45%;
  padding: 5px;
  font-family: 'Exo 2', sans-serif;
  font-family: 'Grandstander', cursive;
  box-sizing: border-box !important;
  border: 1px solid #004d00;
  color: #206040;
  overflow-wrap: break-word;
  float: left;
}
.client{
  border: 1px solid #004d00;
  padding: 10px;
  width: 45%;
  overflow-wrap: break-word;
  float: right;
  color: #bf6040;
}

@media screen and (max-width: 500px){
  .heading{
  text-align: center;
  font-family: 'Grandstander', cursive;
  padding: 10px;
  color:  #00b300;
  margin-top: 10px;
}
.owner{
  width:100%;
  padding: 5px;
  font-family: 'Exo 2', sans-serif;
  font-family: 'Grandstander', cursive;
  box-sizing: border-box !important;
  border: 1px solid #004d00;
  color: #206040;
  overflow-wrap: break-word;
  float: none;
}
.client{
  border: 1px solid #004d00;
  padding: 10px;
  width: 100%;
  overflow-wrap: break-word;
  float: none;
  color: #bf6040;
}

}